import React from "react";
import Layout from "../../../layout";
import { usePartner } from "./hooks";
import { Table } from "../../../components";
import { FaCheckCircle, FaTimesCircle } from "react-icons/fa";

const Partner = () => {
  const data = usePartner();
  const columns = [
    { title: "Name", field: "name" },
    { title: "Email", field: "email" },
    { title: "Phone", field: "phone" },
    { title: "Organization", field: "organization" },
    { title: "Role", field: "role" },
    {
      title: "Monthly Consumption",
      field: "monthlyConsumption",
      render: (item) => <>${item?.monthlyConsumption}</>,
    },
    {
      title: "Existing Subscriber",
      field: "isExistingSubscriber",
      render: (item) => (
        <>
          {item?.isExistingSubscriber ? <FaCheckCircle /> : <FaTimesCircle />}
        </>
      ),
    },
    {
      title: "Currently On Project",
      field: "isCurrentlyOnProject",
      render: (item) => (
        <>
          {item?.isCurrentlyOnProject ? <FaCheckCircle /> : <FaTimesCircle />}
        </>
      ),
    },
    { title: "Earliest Availability", field: "earliestAvailability" },
  ];
  return (
    <Layout name="Registered Partners">
      <Table data={data} columns={columns} />
    </Layout>
  );
};

export default Partner;
